// @flow

import React from 'react';
import styled from 'styled-components';

import Avatar from 'material-ui/Avatar';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';

import FaLinkedinSquare from 'react-icons/lib/fa/linkedin-square';
import FaTwitterSquare from 'react-icons/lib/fa/twitter-square';
import FaGithubSquare from 'react-icons/lib/fa/github-square';

type Props = {};

function AboutMe(props: Props) {
  return (
    <StyledRoot>
      <StyledPaper className="vCard">
        <StyledAvatar alt="José Dulanto" src="/images/josedulanto.jpg" />
        <Typography
          variant="display1"
          color="inherit"
          gutterBottom={false}
          align="center"
        >
          José Dulanto
        </Typography>
        <Typography variant="subheading" gutterBottom={true} align="center">
          I build stuff for the web
        </Typography>
        <Typography className="social-icons" align="center">
          <a
            href="https://www.linkedin.com/in/josedulanto/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FaLinkedinSquare className="linkedin-icon" />
          </a>
          <a
            href="https://github.com/josedulanto"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FaGithubSquare className="github-icon" />
          </a>
          <a
            href="https://twitter.com/josedulanto"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FaTwitterSquare className="twitter-icon" />
          </a>
        </Typography>
        <Typography variant="subheading" paragraph={true}>
          I am a professional Software Engineer with over 6 years of experience
          as a Ruby On Rails Developer working for startups and companies in the
          US and Mexico.
        </Typography>
        <Typography variant="subheading" paragraph={true}>
          Industries where I have had worked developing Ruby On Rails
          applications: Health, Real State, Retail, Agronomics, Blog Networks,
          Strategic Sourcing and, Banquet Halls.
        </Typography>
        <Typography variant="subheading" paragraph={true}>
          Coding is my passion, my daily job, and my hobby. Let me help you code
          the next big thing!
        </Typography>
        <Typography variant="subheading" paragraph={true}>
          Feel free to contact me with any questions you may have.
        </Typography>
      </StyledPaper>
    </StyledRoot>
  );
}

const StyledRoot = styled.div`
  margin-top: 150px;
  z-index: 2;
`;

const StyledAvatar = styled(Avatar)`
  width: 200px !important;
  height: 200px !important;
  position: absolute !important;
  margin: auto;
  left: 0;
  right: 0;
  bottom: 83%;
`;

const StyledPaper = styled(Paper)`
  position: relative;
  padding: 120px 24px 24px 24px;

  .social-icons {
    padding-top: 0;
    padding-bottom: 20px;
    font-size: 30px;

    .linkedin-icon {
      color: #0077b5;
    }

    .github-icon {
      color: #333;
    }

    .twitter-icon {
      color: #1da1f2;
    }
  }
`;

export default AboutMe;



